var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("复仇者联盟")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        “ 复仇者联盟”是由Marvel Comics出版的美国漫画书中出现的一个虚构的超级英雄团队。该团队首次亮相复仇者联盟＃1（1963年9月封面），由作家编辑Stan Lee和艺术家/合作策划人Jack Kirby创作。“复仇者联盟”是Lee和Kirby对前任超级英雄团队All-Winners Squad的翻新，他们出现在由Marvel Comics的前任Timely Comics出版的漫画书系列中。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        标记为“地球上最强大的英雄”，复仇者最初由蚁人，绿巨人，钢铁侠，雷神和黄蜂组成。问题＃2，Ant-Man已成为巨人。最初的美国队长在问题＃4中被发现被困在冰中，并在他们复活后加入了该团体。一个旋转名单成为该系列的标志，虽然一个主题保持一致：复仇者联盟打击“没有单个超级英雄可以承受的敌人”。该小组，著名的它的战斗口号“复仇者集合！”，有特色的人类，突变体，异人族，机器人，外星人，超自然生灵，甚至前恶棍。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        该团队其他衍生剧集包括西海岸复仇者联盟，最初于1984年作为四期短剧出版，随后是102期系列（1985年10月至1994年1月），以＃47 复出复仇者海岸。和40期独奏复仇者联盟（1987年1月 - 1991年1月），用＃21 重新命名复仇者联盟。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在1996年至2004年间，Marvel三次重新启动了主要的复仇者联盟冠军。1996年，“ 英雄重生 ”系列发生在另一个宇宙中，其历史与主流漫威连续性无关。复仇者卷。3从1998年2月到2004年8月重新启动并运行了84期。为了配合本系列的第500期，Marvel改变了编号，复仇者＃500-503（2004年9月 - 12月），在一拍 复仇者决赛（2005年1月）成为了“ 复仇者被拆卸 ”的故事情节和最终的问题。2005年1月，该团队的新版本出现在正在进行的新复仇者联盟中，紧随其后的是强大的复仇者，复仇者联盟：The Initiative和Dark Avengers。复仇者第一卷。4于2010年7月首次亮相，一直持续到2013年1月。 Vol。5在2013年2月推出。在秘密战争之后，一个新的复仇者联盟球队首次亮相，被称为全新的全能复仇者，从免费漫画书日预览开始。在内战二战之后，这本书于2016年重新推出，作为复仇者联盟，同时保留了同一位作家和大部分演员来自全新，全不同的剧本。该系列播出了11个问题，然后再回到原版复仇者的编号问题系列＃672。从问题＃675开始，当时正在发行的所有四部复仇者联盟（复仇者，复仇者复仇者联盟，美国复仇者联盟和占领复仇者联盟）被合并为一个名为复仇者：无投降的每周系列，旨在结束这个团队历史的时代。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
